import Link from 'next/link';
import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
import BottomNavbar from '../../pages/customer/BottomNavbar';

export default function CustomerLayout({ children, disablePadding = false, fullWidth = false }: { children: React.ReactNode; disablePadding?: boolean; fullWidth?: boolean }) {
  const [isDark, setIsDark] = useState<boolean>(false);

  useEffect(() => {
    // Load theme preference
    if (typeof window !== 'undefined') {
      const theme = localStorage.getItem('cc_theme');
      const prefersDark = theme === 'dark' || (!theme && window.matchMedia('(prefers-color-scheme: dark)').matches);
      setIsDark(prefersDark);
      if (prefersDark) {
        document.body.classList.add('theme-dark');
      } else {
        document.body.classList.remove('theme-dark');
      }
    }
  }, []);

  // Export theme toggle function for Profile page
  useEffect(() => {
    if (typeof window !== 'undefined') {
      (window as any).toggleCustomerTheme = () => {
        const newTheme = !isDark;
        setIsDark(newTheme);
        if (newTheme) {
          document.body.classList.add('theme-dark');
          localStorage.setItem('cc_theme', 'dark');
        } else {
          document.body.classList.remove('theme-dark');
          localStorage.setItem('cc_theme', 'light');
        }
      };
    }
  }, [isDark]);

  return (
    <div className={`customer-shell ${fullWidth ? 'full-width' : ''}`}>
      <main className={`customer-main ${disablePadding ? 'no-padding' : ''} page-transition`}>{children}</main>
      <BottomNavbar />
    </div>
  );
}
