import { createContext, useContext, useState, useEffect, ReactNode } from 'react';

export type OnboardingStep = 'EMAIL_VERIFIED' | 'PHONE_PENDING' | 'PHONE_VERIFIED' | null;

export interface User {
    id: string;
    name: string;
    email: string;
    is_onboarded: boolean;
    ui_preferences?: { theme?: 'light' | 'dark' };
}

interface AuthContextType {
    is_authenticated: boolean;
    user: User | null;
    loading: boolean;
    onboarding_step: OnboardingStep;
    customer_id: string | null;
    sign_in: (user_data?: User, token?: string) => void;
    sign_out: () => Promise<void>;
    update_theme: (theme: 'light' | 'dark') => Promise<void>;
    set_onboarding_step: (step: OnboardingStep) => void;
    set_customer_id: (id: string | null) => void;
    get_route_for_step: (step: OnboardingStep) => string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
    // Check for token immediately to prevent "false" unauthenticated state on first frame
    const [is_authenticated, set_is_authenticated] = useState(false);
    const [user, set_user] = useState<User | null>(null);
    const [loading, set_loading] = useState(true);
    const [onboarding_step, set_onboarding_step_state] = useState<OnboardingStep>(null);
    const [customer_id, set_customer_id_state] = useState<string | null>(null);

    useEffect(() => {
        const verify_session = async () => {
            const token = localStorage.getItem('cc_token');

            if (!token) {
                set_loading(false);
                return;
            }

            try {
                const res = await fetch('http://localhost:5000/api/auth/me', {
                    method: 'GET',
                    credentials: 'include',
                    headers: {
                        'Authorization': `Bearer ${token}`
                    }
                });

                if (res.ok) {
                    const data = await res.json();
                    if (data.success && data.data.user) {
                        set_user(data.data.user);
                        set_is_authenticated(true);
                    }
                } else {
                    // Token invalid/expired
                    localStorage.removeItem('cc_token');
                    set_is_authenticated(false);
                    set_user(null);
                }
            } catch (err) {
                console.error('Session verification failed');
            } finally {
                // IMPORTANT: Only set loading to false AFTER the check is done
                set_loading(false);
            }
        };

        const saved_step = localStorage.getItem('cc_onboarding_step');
        if (saved_step && saved_step !== 'null') set_onboarding_step_state(saved_step as OnboardingStep);

        const saved_id = localStorage.getItem('cc_customer_id');
        if (saved_id && saved_id !== 'null') set_customer_id_state(saved_id);

        verify_session();
    }, []);

    const update_theme = async (theme: 'light' | 'dark') => {
        document.body.classList.remove('theme-light', 'theme-dark');
        document.body.classList.add(`theme-${theme}`);
        localStorage.setItem('cc_theme', theme);
        if (is_authenticated) {
            try {
                await fetch('http://localhost:5000/api/customers/ui-preferences', {
                    method: 'PATCH',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ theme }),
                    credentials: 'include'
                });
            } catch (err) { console.error(err); }
        }
    };

    const sign_in = (user_data?: User, token?: string) => {
        if (token) localStorage.setItem('cc_token', token);
        if (user_data) set_user(user_data);
        set_is_authenticated(true);
        set_onboarding_step_state(null);
        localStorage.removeItem('cc_onboarding_step');
    };

    const sign_out = async () => {
        try {
            await fetch('http://localhost:5000/api/auth/logout', {
                method: 'POST',
                credentials: 'include'
            });
        } catch (err) {
            console.error('Logout sync failed:', err);
        } finally {
            localStorage.clear();
            sessionStorage.clear();
            set_is_authenticated(false);
            set_user(null);
            set_onboarding_step_state(null);
            window.location.href = '/customer/signin';
        }
    };

    const value = {
        is_authenticated,
        user,
        loading,
        onboarding_step,
        customer_id,
        sign_in,
        sign_out,
        update_theme,
        set_onboarding_step: (step: OnboardingStep) => {
            set_onboarding_step_state(step);
            localStorage.setItem('cc_onboarding_step', String(step));
        },
        set_customer_id: (id: string | null) => {
            set_customer_id_state(id);
            localStorage.setItem('cc_customer_id', String(id));
        },
        get_route_for_step: (step: OnboardingStep) => {
            if (step === 'PHONE_VERIFIED') return '/customer/home';
            return '/customer/verify-mobile';
        }
    };

    return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) throw new Error('useAuth must be used within AuthProvider');
    return context;
};